@import url(https://fonts.googleapis.com/css?family=Special+Elite|Viga);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,900);
@import url(https://fonts.googleapis.com/css?family=Antic+Didone|Montserrat:700);
body {
  background: #204e4a;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  height: 100vh;
  width: 100vw;
  background: #f2f3d9;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}
  .text{
    display: inline-block;
    font-size: 10vmin;
    line-height: 1.205;
    font-family: 'Special Elite', cursive;
    color: black;
    
  }
  
  .text__first, .text__second{
    position: relative;
  }
  
  .text__word{
    opacity: 0;
  }
  
  .text__first-bg, .text__second-bg{
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  
  .text__first-bg{
    background-color: #204e4a;
  }
  
  .text__second-bg{
    background-color: #ecbf3a;
  }
  
  
  .restart{
    position: absolute;
    font-size: 12px;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgb(25,25,25);
    margin: 0 auto 2%;
    color: white;
  }

.SplitPane {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    min-height: 95vh;
  }
  
  
  .SplitPane-left {
    float: left;
    width: 40%;
    background: #0b4242;
    text-align: center
  }
  
  .SplitPane-right {
    float: left;
    width: 60%;
    background: #f2f3d9;
    color: #0b4242;
    text-align: center
  }
.foto{
  margin-top:10%;
  border-radius: 10px;
  box-shadow:  0 0 15px #001c1b;
  background: #0b4242;
}  

.titulo{
    color: #ecbf3a;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
    margin-top:10%;
    margin-bottom: 0%;
    font-size: 100px;
    text-align: center;
}
.parrafo{
    margin: 10%;
    font-family: 'Montserrat', sans-serif;
}

.boton{
    font-family: 'Montserrat', sans-serif;
    border: 4px solid #ecbf3a;
    color: #ecbf3a;
    font-weight: bold;
    padding: 2%;
    font-size: 20px;
    text-align: center;
}
.boton:hover {
    background-color: #ecbf3a;
    border: 4px solid #fcfcfc;
    color: #fcfcfc;
  }

  @media (max-width: 1000px) {
  .SplitPane {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
    }
  .SplitPane-left {
      float: left;
      width: 100%;
      height: 50%;
  }
  .SplitPane-right {
    float: left;
    width: 100%;
    height: 50%;
    background: #fcfcfc;
    color: #0b4242;
  }
  }
@media screen and (min-width: 760px) {
	:root {
		font-size: 15px;
	}
}
@media screen and (min-width: 820px) {
	:root {
		font-size: 16px;
	}
}
@media screen and (min-width: 900px) {
	:root {
		font-size: 17px;
	}
}
@media screen and (min-width: 980px) {
	:root {
		font-size: 18px;
	}
}
@media screen and (min-width: 1060px) {
	:root {
		font-size: 19px;
	}
}
@media screen and (min-width: 1220px) {
	:root {
		font-size: 20px;
	}
}
@media screen and (min-width: 1400px) {
	:root {
		font-size: 22px;
	}
}

.wrapper {
	width: 100%;
	height: 100vh;
	margin: 0;
	padding: 0;
	position: relative;
	-webkit-animation: fadeIn 500ms ease-in-out forwards;
	        animation: fadeIn 500ms ease-in-out forwards;
	background-size: cover;
	transition: background-image 500ms ease-in-out;
}

.selectors {
	position: absolute;
	top: 0;
	left: 0;
	width: 30px;
	background: rgba(0, 0, 0, 0.1);
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	z-index: 1;
	
}
.selector {
	border: 1px solid #fff;
	background: none;
	width: 11px;
	height: 11px;
	border-radius: 50%;
	margin: 5px 0;
	opacity: 0.6;
	cursor: pointer;
	transition: background .3s ease-in-out, opacity .3s ease-in-out;
	
}
.selector:hover,
.selector.active {
		background: white;
		opacity: 0.9;
}

