aside.panel {
	width: 40vw;
	height: 100%;
	opacity: 0.9;
	/* color: #fff; */
	box-shadow: 5px 0 25px 0 rgba(0, 0, 0, 0.3);
	transition: background 500ms ease-in-out;
	
}
h2.panel-header,
	p.panel-info {
		margin: 0;
		padding: 1.3rem 3.5rem;
	}
	h2.panel-header {
		padding-top: 2em;
		font-weight: normal;
		font-size: 2.5rem;
		text-transform: capitalize;
	}

	p.panel-info {
		font-size: 1.2rem;
		line-height: 1.8em;
	}

	button.panel-button {
		margin: 1.3rem 3.5rem;
		padding: 0.8em 1.3em;
		background: none;
		border: 1px solid;
		/*color: #fff; */
		cursor: pointer;
		font-family: 'Montserrat';
		font-size: 0.8rem;
		font-weight: 700;
		text-transform: uppercase;
		transition: box-shadow 0.5s ease-in-out, color 0.5s ease-in-out;
		
	}
	button.panel-button:hover {
		box-shadow: inset 0 0 1.5em 1.5em #fff;
	}
	button.panel-button:focus {
		outline: none;
	}
    .rotar {
		margin-bottom: 150%;
		text-transform:uppercase;
		color: #fff;
        -webkit-transform: rotate(-90deg);
		transform: rotate(-90deg);
    }

@-webkit-keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.section{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
}


